/* app.css - Современные стили для социальной сети геймеров */
:root {
  --primary-bg: #0a0e17;
  --secondary-bg: #13182d;
  --accent-primary: #7a04eb;
  --accent-secondary: #00d2ff;
  --accent-tertiary: #fe3a7f;
  --neon-glow: 0 0 10px rgba(122, 4, 235, 0.7),
               0 0 20px rgba(122, 4, 235, 0.5),
               0 0 30px rgba(122, 4, 235, 0.3);
  --text-primary: #ffffff;
  --text-secondary: #b4b9d6;
  --glass-bg: rgba(19, 24, 45, 0.6);
  --glass-border: rgba(255, 255, 255, 0.1);
  --success: #00ff8c;
  --warning: #ffcc00;
  --danger: #ff3860;
}

/* Базовые стили и сброс */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Rajdhani', 'Exo 2', 'Segoe UI', sans-serif;
  background: var(--primary-bg);
  color: var(--text-primary);
  line-height: 1.6;
  overflow-x: hidden;
  position: relative;
  min-height: 100vh;
}

/* Анимированный градиентный фон с плавающими частицами */
.animated-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  overflow: hidden;
}

.gradient-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(125deg, 
    #0a0e17 0%, 
    #13182d 30%, 
    #1a1f3a 50%, 
    #24294a 70%, 
    #2e3461 100%);
  background-size: 400% 400%;
  animation: gradientShift 15s ease infinite;
}

@keyframes gradientShift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

.floating-particles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.particle {
  position: absolute;
  border-radius: 50%;
  background: rgba(122, 4, 235, 0.15);
  animation: float 15s infinite ease-in-out;
}

.particle:nth-child(1) {
  width: 80px;
  height: 80px;
  top: 20%;
  left: 10%;
  animation-delay: 0s;
  box-shadow: 0 0 40px rgba(122, 4, 235, 0.3);
}

.particle:nth-child(2) {
  width: 120px;
  height: 120px;
  top: 60%;
  left: 80%;
  animation-delay: -2s;
  box-shadow: 0 0 60px rgba(0, 210, 255, 0.3);
}

.particle:nth-child(3) {
  width: 60px;
  height: 60px;
  top: 70%;
  left: 20%;
  animation-delay: -4s;
  box-shadow: 0 0 30px rgba(254, 58, 127, 0.3);
}

.particle:nth-child(4) {
  width: 100px;
  height: 100px;
  top: 30%;
  left: 65%;
  animation-delay: -6s;
  box-shadow: 0 0 50px rgba(122, 4, 235, 0.3);
}

.particle:nth-child(5) {
  width: 90px;
  height: 90px;
  top: 50%;
  left: 40%;
  animation-delay: -8s;
  box-shadow: 0 0 45px rgba(0, 210, 255, 0.3);
}

@keyframes float {
  0%, 100% { 
    transform: translateY(0) translateX(0) scale(1); 
    opacity: 0.7;
  }
  25% { 
    transform: translateY(-20px) translateX(15px) scale(1.05); 
    opacity: 0.9;
  }
  50% { 
    transform: translateY(10px) translateX(-15px) scale(0.95); 
    opacity: 0.6;
  }
  75% { 
    transform: translateY(-10px) translateX(-10px) scale(1.02); 
    opacity: 0.8;
  }
}

.pulsating-circle {
  position: absolute;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  background: rgba(122, 4, 235, 0.1);
  top: 50%;
  left: 70%;
  transform: translate(-50%, -50%);
  animation: pulse 8s infinite ease-in-out;
  box-shadow: 0 0 100px rgba(122, 4, 235, 0.2);
}

@keyframes pulse {
  0%, 100% { 
    transform: translate(-50%, -50%) scale(1); 
    opacity: 0.5;
  }
  50% { 
    transform: translate(-50%, -50%) scale(1.3); 
    opacity: 0.8;
  }
}

/* Стеклянный морфизм (Glassmorphism) */
.glass-container {
  background: var(--glass-bg);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid var(--glass-border);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.glass-panel {
  background: rgba(19, 24, 45, 0.7);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  padding: 24px;
  transition: all 0.3s ease;
}

.glass-card {
  background: rgba(19, 24, 45, 0.5);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 12px;
  transition: all 0.3s ease;
}

.glass-container:hover,
.glass-panel:hover,
.glass-card:hover {
  border-color: rgba(122, 4, 235, 0.3);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
}

/* Неоновые подсветки */
.neon-border {
  border: 1px solid rgba(122, 4, 235, 0.3);
  box-shadow: var(--neon-glow);
  animation: neonPulse 2s infinite alternate;
}

@keyframes neonPulse {
  from {
    box-shadow: 0 0 5px rgba(122, 4, 235, 0.5),
                0 0 10px rgba(122, 4, 235, 0.3),
                0 0 15px rgba(122, 4, 235, 0.2);
  }
  to {
    box-shadow: 0 0 10px rgba(122, 4, 235, 0.7),
                0 0 20px rgba(122, 4, 235, 0.5),
                0 0 30px rgba(122, 4, 235, 0.3);
  }
}

.neon-text {
  color: var(--text-primary);
  text-shadow: 0 0 5px rgba(122, 4, 235, 0.7),
               0 0 10px rgba(122, 4, 235, 0.5),
               0 0 15px rgba(122, 4, 235, 0.3);
  animation: textGlow 2s infinite alternate;
}

@keyframes textGlow {
  from {
    text-shadow: 0 0 5px rgba(122, 4, 235, 0.5),
                 0 0 10px rgba(122, 4, 235, 0.3);
  }
  to {
    text-shadow: 0 0 10px rgba(122, 4, 235, 0.7),
                 0 0 20px rgba(122, 4, 235, 0.5),
                 0 0 30px rgba(122, 4, 235, 0.3);
  }
}

.neon-accent {
  color: var(--accent-secondary);
  text-shadow: 0 0 5px rgba(0, 210, 255, 0.7),
               0 0 10px rgba(0, 210, 255, 0.5);
}

/* Кнопки с градиентами и свечением */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 12px 28px;
  border: none;
  border-radius: 50px;
  font-family: inherit;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  outline: none;
}

.btn-primary {
  background: linear-gradient(135deg, var(--accent-primary) 0%, var(--accent-tertiary) 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(122, 4, 235, 0.4);
}

.btn-primary:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(122, 4, 235, 0.6);
}

.btn-primary:active {
  transform: translateY(1px);
  box-shadow: 0 2px 10px rgba(122, 4, 235, 0.4);
}

.btn-secondary {
  background: transparent;
  color: var(--text-primary);
  border: 1px solid var(--accent-primary);
  box-shadow: 0 0 10px rgba(122, 4, 235, 0.3);
}

.btn-secondary:hover {
  background: rgba(122, 4, 235, 0.1);
  box-shadow: 0 0 15px rgba(122, 4, 235, 0.5);
  transform: translateY(-2px);
}

.btn-neon {
  background: transparent;
  color: var(--accent-secondary);
  border: 1px solid var(--accent-secondary);
  box-shadow: 0 0 10px rgba(0, 210, 255, 0.3);
  animation: neonPulse 2s infinite alternate;
}

.btn-neon:hover {
  background: rgba(0, 210, 255, 0.1);
  box-shadow: 0 0 20px rgba(0, 210, 255, 0.5);
  transform: translateY(-2px);
}

/* Плавные переходы при наведении и кликах */
.hover-lift {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.hover-lift:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
}

.hover-glow {
  transition: box-shadow 0.3s ease;
}

.hover-glow:hover {
  box-shadow: 0 0 20px rgba(122, 4, 235, 0.4);
}

/* Анимации появления элементов */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.animate-fade-in {
  animation: fadeIn 0.6s ease forwards;
}

.animate-slide-left {
  animation: slideInLeft 0.6s ease forwards;
}

.animate-slide-right {
  animation: slideInRight 0.6s ease forwards;
}

.animate-scale {
  animation: scaleIn 0.5s ease forwards;
}

.delay-1 { animation-delay: 0.1s; }
.delay-2 { animation-delay: 0.2s; }
.delay-3 { animation-delay: 0.3s; }
.delay-4 { animation-delay: 0.4s; }
.delay-5 { animation-delay: 0.5s; }

/* Эффекты параллакса */
.parallax-container {
  position: relative;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  perspective: 1px;
}

.parallax-layer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.parallax-back {
  transform: translateZ(-1px) scale(2);
}

.parallax-base {
  transform: translateZ(0);
}

.parallax-front {
  transform: translateZ(1px) scale(0.5);
}

/* Стили для навигации */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  padding: 1rem 2rem;
  background: rgba(10, 14, 23, 0.8);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(122, 4, 235, 0.2);
}

.nav-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1400px;
  margin: 0 auto;
}

.logo {
  display: flex;
  align-items: center;
  font-size: 1.8rem;
  font-weight: 700;
  color: var(--text-primary);
  text-decoration: none;
}

.logo span {
  color: var(--accent-primary);
}

.nav-menu {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.nav-link {
  color: var(--text-secondary);
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
  position: relative;
}

.nav-link:hover {
  color: var(--text-primary);
}

.nav-link::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, var(--accent-primary), var(--accent-secondary));
  transition: width 0.3s ease;
}

.nav-link:hover::after {
  width: 100%;
}

/* Стили для карточек игроков */
.player-card {
  background: var(--glass-bg);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid var(--glass-border);
  border-radius: 16px;
  padding: 1.5rem;
  transition: all 0.3s ease;
  animation: fadeIn 0.6s ease forwards;
}

.player-card:hover {
  transform: translateY(-5px);
  border-color: rgba(122, 4, 235, 0.3);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2), var(--neon-glow);
}

.player-avatar {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid var(--accent-primary);
  box-shadow: 0 0 15px rgba(122, 4, 235, 0.5);
}

.player-name {
  font-size: 1.4rem;
  font-weight: 700;
  margin: 1rem 0 0.5rem;
  color: var(--text-primary);
}

.player-level {
  display: inline-block;
  padding: 0.2rem 0.8rem;
  background: linear-gradient(135deg, var(--accent-primary) 0%, var(--accent-tertiary) 100%);
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
  color: white;
}

.player-stats {
  display: flex;
  justify-content: space-around;
  margin: 1rem 0;
  padding: 1rem 0;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.stat {
  text-align: center;
}

.stat-value {
  font-size: 1.4rem;
  font-weight: 700;
  color: var(--accent-secondary);
}

.stat-label {
  font-size: 0.8rem;
  color: var(--text-secondary);
}

/* Стили для постов/контента */
.post-card {
  background: var(--glass-bg);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid var(--glass-border);
  border-radius: 16px;
  padding: 1.5rem;
  margin-bottom: 2rem;
  transition: all 0.3s ease;
}

.post-card:hover {
  border-color: rgba(122, 4, 235, 0.3);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}

.post-header {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
}

.post-avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid var(--accent-primary);
  margin-right: 1rem;
}

.post-author {
  font-weight: 700;
  color: var(--text-primary);
}

.post-time {
  font-size: 0.8rem;
  color: var(--text-secondary);
}

.post-content {
  margin-bottom: 1rem;
  line-height: 1.6;
}

.post-image {
  width: 100%;
  border-radius: 12px;
  margin-bottom: 1rem;
}

.post-actions {
  display: flex;
  justify-content: space-around;
  padding-top: 1rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.action-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: none;
  border: none;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.3s ease;
  padding: 0.5rem 1rem;
  border-radius: 8px;
}

.action-btn:hover {
  color: var(--accent-primary);
  background: rgba(122, 4, 235, 0.1);
}

/* Стили для игровых виджетов */
.game-widget {
  background: var(--glass-bg);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid var(--glass-border);
  border-radius: 16px;
  padding: 1.5rem;
  margin-bottom: 2rem;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.game-widget::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background: linear-gradient(90deg, var(--accent-primary), var(--accent-secondary));
}

.game-widget:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

.game-title {
  font-size: 1.4rem;
  font-weight: 700;
  margin-bottom: 1rem;
  color: var(--text-primary);
}

.game-stats {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.game-stat {
  background: rgba(19, 24, 45, 0.5);
  padding: 1rem;
  border-radius: 12px;
  text-align: center;
}

.game-stat-value {
  font-size: 1.8rem;
  font-weight: 700;
  color: var(--accent-secondary);
  margin-bottom: 0.5rem;
}

.game-stat-label {
  font-size: 0.9rem;
  color: var(--text-secondary);
}

/* Адаптивный дизайн */
@media (max-width: 1200px) {
  .game-stats {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 992px) {
  .nav-menu {
    position: fixed;
    top: 0;
    right: -100%;
    width: 280px;
    height: 100vh;
    background: var(--secondary-bg);
    flex-direction: column;
    padding: 4rem 2rem;
    transition: right 0.3s ease;
    z-index: 999;
  }
  
  .nav-menu.active {
    right: 0;
  }
  
  .menu-toggle {
    display: block;
    z-index: 1000;
  }
}

@media (max-width: 768px) {
  .navbar {
    padding: 1rem;
  }
  
  .player-stats {
    flex-direction: column;
    gap: 1rem;
  }
  
  .post-actions {
    flex-wrap: wrap;
    gap: 0.5rem;
  }
  
  .action-btn {
    flex: 1;
    justify-content: center;
  }
}

@media (max-width: 576px) {
  .logo {
    font-size: 1.5rem;
  }
  
  .player-card,
  .post-card,
  .game-widget {
    padding: 1rem;
  }
  
  .player-avatar {
    width: 60px;
    height: 60px;
  }
  
  .player-name {
    font-size: 1.2rem;
  }
}

/* Утилитарные классы */
.text-gradient {
  background: linear-gradient(135deg, var(--accent-primary) 0%, var(--accent-secondary) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.glow-effect {
  position: relative;
}

.glow-effect::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0 0 30px rgba(122, 4, 235, 0.4);
  border-radius: inherit;
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: -1;
}

.glow-effect:hover::after {
  opacity: 1;
}

.cyberpunk-border {
  position: relative;
  border: 1px solid transparent;
  background: linear-gradient(var(--secondary-bg), var(--secondary-bg)) padding-box,
              linear-gradient(45deg, var(--accent-primary), var(--accent-tertiary)) border-box;
}

/* Анимация загрузки */
.loading-spinner {
  width: 50px;
  height: 50px;
  border: 3px solid rgba(122, 4, 235, 0.3);
  border-radius: 50%;
  border-top-color: var(--accent-primary);
  animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Кастомный скроллбар */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--secondary-bg);
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, var(--accent-primary) 0%, var(--accent-tertiary) 100%);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, var(--accent-tertiary) 0%, var(--accent-primary) 100%);
}